import Notebook from '@/api/notebooks'
import {Message} from "element-ui";
import el from "element-ui/src/locale/lang/el";
import notebooks from "../../api/notebooks";


const state = {
  notebooks: null,
  currentBookId: null
}

const getters = {
  notebooks: state => state.notebooks || [],
  currentBook: state => {
    if(!Array.isArray(state.notebooks)){ // 如果 state.notebooks 不是数组
      return {}
    }
    if(!state.currentBookId){
      return state.notebooks[0] || {}
    }else{
      return state.notebooks.find(notebook => notebook.id === parseInt(state.currentBookId)) || {}
    }
  }
}

const mutations = {
  setNotebook(state, payload) {
    state.notebooks = payload.notebooks
  },
  addNotebook(state, payload) {
    state.notebooks.unshift(payload.notebook)
  },
  updateNotebook(state, payload) {
    let notebook = state.notebooks.find(notebook => notebook.id === parseInt(payload.notebookId)) || {}
    notebook.title = payload.title
  },
  deleteNotebook(state, payload) {
    state.notebooks = state.notebooks.filter(notebook => notebook.id !== parseInt(payload.notebookId))
  },
  setCurrentBook(state, payload){
    state.currentBookId = payload.currentBookId
  }
}

const actions = {
  getNotebooks({commit}) {
    if(state.notebooks !== null) {
      return Promise.resolve()
    }
    return Notebook.getAll().then(res => {
      commit('setNotebook', {notebooks: res.data})
    })
  },
  addNotebook({commit}, payload) {
    return Notebook.addNotebook({title: payload.title})
      .then(res => {
        commit('addNotebook', {notebook: res.data})
        Message.success(res.msg)
      })
  },
  updateNotebook({commit}, payload) {
    return Notebook.updateNotebook(payload.notebookId, {title: payload.title}).then(res => {
      commit('updateNotebook', {notebookId: payload.notebookId, title: payload.title})
      Message.success(res.msg)
    })
  },
  deleteNotebook({commit}, payload) {
    return Notebook.deleteNotebook(payload.notebookId)
      .then(res => {
        commit('deleteNotebook', {notebookId: payload.notebookId})
        Message.success(res.msg)
      })
  }
}

export default {
  state,
  getters,
  mutations,
  actions
}



