// 使用模块化机制编程，导入 Vue 和 VueRouter， 要调用 Vue.use(VueRouter)
import Vue from 'vue'
import Router from 'vue-router'

/*import Login from '@/components/Login'
import Notebooks from '@/components/NotebookList'
import NoteDetail from '@/components/NoteDetail'
import TrashDetail from '@/components/TrashDetail'
import HelloWorld from '@/components/HelloWorld'*/


Vue.use(Router)


// 3. 创建 router 实例，然后传 routes 配置
export default new Router({
  // 2. 定义路由
  // @表示我们的 src 目录

  // 优化
  // 1. 重定向和别名
  // alias: '/notebooks', 表示/ 的别名是 /notebooks，当用户访问 /时，URL 会保持为 /，但是路由匹配则为 /notebooks
  // 2. 懒加载
  // 改为 component: () => import('@/components/NotebookList.vue'),并且把上面的 import 都删了
  routes: [
    {
      path: '/',
      alias: '/notebooks',
      component: () => import('@/components/NotebookList.vue')
    },
    {
      path: '/login',
      name: 'Login',
      component: () => import('@/components/Login.vue')
    },
    {
      path: '/notebooks',
      name: 'Notebooks',
      component: () => import('@/components/NotebookList.vue')
    },
    {
      // path: '/note/:noteId',
      path: '/note',
      name: 'NoteDetail',
      component: () => import('@/components/NoteDetail.vue')
    },
    {
      // path: '/trash/:noteId',
      path: '/trash',
      name: 'TrashDetail',
      component: () => import('@/components/TrashDetail.vue')
    },
  ]
})



